  max-width: 1000px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
  background-color: rgb(93, 156, 168);

}
.input {
  font-size: 200px;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

.btnn {
  display: inline-block;
  background: steelblue;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btnn:focus {
  outline: none;
}

.btnn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btnn-block {
  display: block;
  width: 100%;
}

.quiz {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.quiz.reminder {
  border-left: 5px solid green;
}

.quiz h3 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.form-control-check label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.form-control-check input {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

.float-container {
  border: 3px solid #fff;
  width: auto;
  /* padding: 10px; */
}

.float-child {
  width: 80%;
  float: left;
  padding: 10px;
  border: 1px solid steelblue;
}

.float-container-noborder {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.float-child-noborder {
  width: 15%;
  float: left;
  padding: 5px;
}
.float-child-noborder-answers {
  width: 25%;
  float: left;
  padding: 5px;
}

/* spinner */
.spinner {
  width: 55px;
  height: 55px;
  position: relative;
}
.spinner div {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  border: 8px solid transparent;
  border-top-color: steelblue;
  border-radius: 50%;
  -webkit-animation: spinnerOne 1.2s linear infinite;
          animation: spinnerOne 1.2s linear infinite;
}
.spinner div:nth-child(2) {
  border: 8px solid transparent;
  border-bottom-color: steelblue;
  -webkit-animation: spinnerTwo 1.2s linear infinite;
          animation: spinnerTwo 1.2s linear infinite;
}

@-webkit-keyframes spinnerOne {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 10px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 1px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 10px; }
}

@keyframes spinnerOne {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 10px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 1px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 10px; }
}
@-webkit-keyframes spinnerTwo {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 1px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 10px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 1px; }
}
@keyframes spinnerTwo {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 1px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 10px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 1px; }
}

